<template>
  <div class="row">
    <form @submit.prevent="onSubmit" class="col l6 m8 s12 offset-l3 offset-m2 offset-s0">
      <div class="row">
        <h5>2. Выберите количество смен</h5>
        <span class="note">Без подработок</span>
      </div>
      <div class="input row">
        <div class="input-field col s9">
          <input id="shifts" type="text" class="validate" required v-model="shifts" />
          <label for="shifts">Количество смен</label>
          <span class="helper-text" data-error="Заполните это поле"></span>
        </div>
        <button type="button" class="btn btn-count red" @click="changeShifts('down')">-</button>
        <button type="button" class="btn btn-count btn-plus" @click="changeShifts('up')">+</button>
      </div>

      <div class="row">
        <div class="input-field col s9">
          <input id="hights" type="text" class="validate" required v-model="nights" />
          <label for="hights">Из них ночных</label>
          <span class="helper-text" data-error="Заполните это поле"></span>
        </div>
        <button type="button" class="btn btn-count red" @click="changeNights('down')">-</button>
        <button type="button" class="btn btn-count btn-plus" @click="changeNights('up')">+</button>
      </div>

      <ControlButtons linkBack="/" nextText="Следующий шаг" />
    </form>
  </div>
</template>

<script>
import ControlButtons from "@/components/ControlButtons";
export default {
  name: "shifts",
  components: { ControlButtons },
  data: () => ({
    shifts: 15,
    nights: 7
  }),
  mounted() {
    M.updateTextFields();
  },
  methods: {
    changeShifts(type) {
      if (type === "down") this.shifts--;
      else this.shifts++;
    },
    changeNights(type) {
      if (type === "down") this.nights--;
      else this.nights++;
    },
    onSubmit() {
      let shifts = {
        total: this.shifts,
        nights: this.nights
      };
      this.$store.dispatch("setShifts", shifts);
      this.$router.push("/overtime");
    }
  }
};
</script>

<style lang='scss' scoped>
.note {
  color: #aaa;
}
.btn-plus {
  float: right;
}
.btn-count {
  margin-top: 1rem;
}
</style>